<template id="Result">
  <div>
    <p> 风险评估结果 </p>
    <p>实体名称：{{keyword}}</p>
    <div class="div1"><table><tr><td><view1></view1></td><td> <view2></view2></td><td><view3></view3></td></tr></table></div>
    <div class="div2"><table><tr><td><map1></map1></td></tr></table></div>
    <div class="div3"><table><tr><td><view4></view4></td><td><info1></info1></td><td><info2></info2></td><td><info3></info3></td></tr></table>
   </div>
   </div>
</template>
<script type = "text/javascript" src = "http://api.map.baidu.com/api?v=2.0&ak=RIZ5gVBQ2k370IOE3oFGg8HNLaSHDwB2"></script>
<script>
export default {
  components: {
    view1: require('./view1.vue').default,
    view2: require('./view2.vue').default,
    view3: require('./view3.vue').default,
    view4: require('./view4.vue').default,
    info1: require('./info1.vue').default,
    info2: require('./info2.vue').default,
    info3: require('./info3.vue').default,
    map1: require('./map1.vue').default
  },
  computed: {
    keyword () {
      return this.$store.state.keyword
    }
  }
}
</script>

<style>
.div1{
   margin:0 auto;
    width:100%
}
.div2{
   margin:0 auto;
    width:100%
}
.div3{
   margin:0 auto;
    width:100%
}


</style>



